.team-section{
    width: 100%;
    height: 100%;
    min-height: 100vh;
    position: relative;

    .team-background{
        width: 100%;
        height: 100%;
        position: absolute;
z-index: -1;    }

.team-title{
    width: 100%;
    padding: 0 100px;
        h1{
            margin: 0;
            text-align: center;
            color: $VioletColor;
            font-weight:bolder;
            font-size:3.5vw;
            border-bottom:2px solid $OrangeColor
        }
    
    }
.team-container{
    display:flex;
    width:100%;
    height:100%;
    min-height: 100vh;
    position:relative;
   padding: 50px 100px;
   justify-content: space-between;
   
align-items: center;
   .team-content{
       height: 100%;
       width: 30%;
       display: flex;
      justify-content: space-around;
       flex-direction: column;
       align-items: center;
       text-align: center;
       
       .team-person-image{
           width:80%;
       }
       h1{
           margin: 0 ;
         
           text-align: center;
           color: $VioletColor;
           font-size: 2vw;
       }
       h2{
           margin: 0;
         
           color: $OrangeColor;
           font-size: 1.5vw;
           font-weight: 600;
       }
      
   }
   .line-team{
    width: .4%;
   height: 90%;
}
}




}