.about-who-are{
    position: relative;
    width: 100%;
    height: 100%;
    min-height: 100vh;

     .who-are-bg{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        
        z-index: -1;
        

    }
    .who-are-container{
        position: relative;
        display: flex;
        width: 100%;
        height: 100%;
        min-height: 100vh;
        
        padding: 0 100px;
        align-items: center;
        justify-content: space-around;
        .who-are-text{
          
            h1{
                color: $VioletColor;
                text-align: center;
                font-size: 3vw;
                margin: 0;
            }
            h2{
                color: $OrangeColor;
                text-align: center;
                font-size: 2.1vw;
                margin: 0;
            }
            h3{
                margin: 0;
                color:$VioletColor;
                text-align: center;
                font-size: 2vw;
                font-weight: bold;
                
            }
        }
         .who-are-image{
         
         width: 20vw;
            img{width: 100%;}
        }
    }

}