

.hero-section {
  width: 100%;
  height:100%;
  min-height: 100vh;
  position: relative;
  
}

.hero-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}
.hero-content {
  width: 100%;
  height: 100%;
min-height: 100vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 100px;
  .behind-text{
    position: absolute;
    top: 00px;
    left: 200px;
    z-index: -1;
    
    object-fit: contain;
    width: 50%;
    
  }
  .hero-text{
    
  
    h1{
      color: $SloganColor;
      font-size: 4vw;
    line-height: normal;
    }
    h2{
      color: $SloganColor;
      font-size: 3vw;
      line-height: 0px;
    }
    .hero-btn{
   
      margin-top: 12vh;
    }
  }
}
.hero-image{
  width: 30vw;
  img{
    width: 100%;
  }
}
