.vision-section{
    width: 100%;
    height: 100%;
    min-height: 100vh;
    position: relative;
  
 .vision-image{
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: -1;
}
.arrow-services{
    text-align: center;
 h1{
     color: $VioletColor;
     font-size: 1vw;
 }   
 .arrow-image{
     width: 1%;
 }
 

}

}
.vision-title{
  

    text-align: center;
    position: relative;
    padding-top: 1px;

    

    h1{
        
        color: $VioletColor;
        font-size:4vw;
        font-weight: bolder;
        line-height: normal;
    }
    h2{
        line-height: 0;
        color: $OrangeColor;
        font-size: 3vw;
        font-weight: bolder;
       
    }
}
.vision-container{
    width: 80%;
    margin: auto auto;
    display: flex;
    height:70%;
    min-height: 50vh;
  
    
    align-items: center;

    .vision-text{
      
        h1{
            color: $VioletColor;
font-size: 1.5vw;
        }
        width: 50%;

    }
    .image-right-vision{
        width: 50vw;
        img{
            width: 100%;
        }
    }

}






