.our-product{
    width: 100%;
    height: 100%;
    min-height: 100vh;
 
    
position: relative;
   .product-background{
        position: absolute;
        z-index: -1;
        width: 100%;
        height: 100%;
    }
    .product-title{
        position: relative;
               display: flex;
              align-items: flex-end;
               justify-content: space-between;
            
               margin: auto auto;
               width: 90%;
               
               border-bottom: 3px solid $OrangeColor;
                
               h1{
                 
             color: $VioletColor;
                   font-weight: bolder;
                   font-size: 3vw;
          line-height: 10px;
               }
               h2{
                 line-height: 0;
                   color: $VioletColor;
                   border-bottom-color:$OrangeColor ;
}}}


.product-container{
    
width: 100%;
height: 100%;
min-height: 100vh;
display: flex;
 align-items: center;
 justify-content: space-around;

padding: 50px 150px;
    
}

.product-content{
    
   display: flex;
   flex-direction: column;
   align-items: center;
   h1{
    
       color: $VioletColor;
       font-size:2vw;
   }
   .product-image{
       width: 80%;
       img{
           width: 100%;
       }
   }
    
}
.bottom-border{
    width: 80%;
    margin: auto auto;
    border: 3px dashed $OrangeColor;
}
