.footer-section{

    width: 100%;
    height: 200px;
    position: absolute;
    display: flex;


  img{
      position: absolute;
      width: 100%;
      height: 100%;
      z-index: -1;
  }
 

}
.footer-image{
    width: 20%;

height: 100px;
position: relative;
display: flex;
align-self: flex-end;


    // width: 300px;
    // height:100px;

    // padding: 0px 90px;
    img{
        width: 100%;
       
    }
 
}
.footer-container{

    width: 60%;
    display: flex;
  background-color: red;
  display: flex;
  margin-left: 250px;
  margin-top: 80px;

  .nav-bottom{
      width: 30%;
      

      border-right: 1px solid white ;
      h1{
          margin: 0;
          color: $OrangeColor;
          text-decoration: underline;
      }
      .link-bottom{
          font-size: 1rem;
          display: flex;
          padding: 5px 10px;
          justify-content: space-between;
          
         
         a
         {
           
             color: white;
            text-decoration: none;
            font-weight: bolder;
        }
      }
  }


  .footer-contact{
      width: 30%;
      h1{
        margin: 0;
        color: $OrangeColor;
        text-decoration: underline;
      }
  }
}
