.contact-section{
    width: 100%;
    height: 100%;
    min-height: 100vh;
    position: relative;


     .contact-background{
        position: absolute;
        width: 100%;
        height: 100%;
        z-index: -1;
    }
    .contact-container{
        width: 100%;
        height: 100%;
        min-height: 100vh;
        display: flex;
        padding: 0 100px;
        justify-content: space-between;
        align-items: center;


    }
   
}

.contact-form{    
  position: relative;
width: 50%;
    h1{
        padding: 0 20px;
        margin: 0;
        font-size: 1.5vw;
        color: $OrangeColor;
    }
}
.spins{
    position: relative;
    display: flex;
    justify-content: space-between;
    padding: 0 10px;
    .spin{
        border-radius: 50%;
        width: 20px;
        height: 20px;
        background-color: $OrangeColor;
        border:2px solid $VioletColor
    }
}
.form-content{
    display: flex;
    flex-direction: column;
    padding: 0 10px;
    .form-title{
        display: flex;
align-items: center;
        img{
            position: relative;
            z-index: 999;
            width: 20%;
       
        }
        h1{
            color: $VioletColor;
            font-size:2vw;
            line-height: 0;
        }
    }
}
.btn-contactUs{
 
    text-align: center;
}






.contact-image{
z-index: 999;
position: relative;
width: 50%;

img{
position: relative;
width:100%;
}}