
.services-section{
    width: 100%;  
overflow-y: scroll;
   position: relative;
  height: 100%;
min-height: 100vh;
 .services-bg{
    width: 100%;
  height: calc(100% / 3);
  
    position: absolute;
    z-index: -1;
}
}
.services-section::-webkit-scrollbar{
    display: none;
}
.services-container{
   
    display: flex;
    width: 100%;
    height: 100%;
    min-height: 100vh;
    justify-content: space-between;
    padding: 0 100px;
    
    .services-content{
        width: 60%;
   height: 100%;
   min-height: 100vh;
        display: flex;
        flex-direction: column;
        .services-image{
            width: 100%;
         
            img{width: 100%;
            height: 100%;}
        }
        .services-text{
            width: 100%;
            
            
            h2{
                // color: $VioletColor;
                font-size: 1vw;
            }
        }
        

.services-title{
width: 55%;
margin-bottom: 10px;
    // display: flex;
    h1{
 margin: 0;
        font-size: 4vw;
        color: $VioletColor;
        font-weight: bolder;
        border-bottom: 2px solid $OrangeColor;
       
    }
   
    
}
    }

    .services-right{
        width: 40%;
        display: flex;
    
        .scroll-image{
           
            width: 15%;
            height: 100%;
            min-height: 100vh;
            img{
                height: 100%;
                width: 100%;
            }
          
            }
            .scroll-title{
                width: 100%;
         
          margin: auto auto;
         
         h1{
                    color: $VioletColor;
                    font-size: 3vw;
                    line-height: 2px;
                }
                h2{
                    color: $OrangeColor;
                    line-height: 0;
                    font-size: 1vw;
                }
            
        }
        }
       
}





