.about-company{
    position: relative;
    width: 100%;
    height: 100%;
    min-height: 100vh;
    color: red;
    .about-company-title{
        width: 100%;
        text-align: center;
    }
    h1{
        text-align: center;
        margin: 0;
        color: $VioletColor;
        font-size: 3vw;

    }
    .about-company-bg{
        position: absolute;
        z-index: -1;
        height: 100%;
        width: 100%;
        
  
    }
    .about-company-container{
     
        height: 100%;
        width: 100%;
        // min-height: 100vh;
        position: relative;
     align-items: center;
        justify-content: space-between;
     
        display: flex;
        padding: 0 100px;
        .about-company-image{
            width: 50%;
            img{width: 100%;}
        }
        .about-company-text{
            width: 50%;
    
            h1{
                color: $VioletColor;
                font-size: 2vw;
                text-align: start;
            }
        }
    }
   
}